import { useTranslation } from "react-i18next";
import style from "../../../InstituteFeedback/InstituteFeedback.module.css";
import { Link, useLocation, useParams } from "react-router-dom";

const StreamClassMasterCard = ({ cls }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  return (
    <Link
      to={`/q/${params.username}/automate/institute?type=standard-subject`}
      state={{
        ...getQuery.state,
        clsId: cls?._id,
        clsName: cls?.className,
      }}
    >
      <div className={style.sf_card}>
        <div className={style.sf_card_contatiner}>
          <div className={style.sf_card_contatiner_inner}>
            <h6
              style={{
                marginBottom: "0",
              }}
            >
              {t("class_name")}
              {": "}
              {cls?.className ?? ""}
            </h6>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default StreamClassMasterCard;
