import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import UserMemberContainerWrapper from "../../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUtilityUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
import HorizontalTabWrapper from "../../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import { Suspense, useState } from "react";
import QvipleLoader from "../../../../../Loader/QvipleLoader";
import style from "../../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import SubjectMasterCo from "./SubjectMasterCo";
import SubjectMasterTeachingPlan from "./SubjectMasterTeachingPlan";
import UploadExcelButton from "../../../../../Ui/Button/UploadExcelButton";
import { useFileUpload } from "../../../../../Hooks/ApiHooks/Auth/auth-api";
import {
  useAutomateInstituteStreamSubjectMasterCo,
  useAutomateInstituteStreamSubjectMasterTeachingPlan,
} from "../../../../../Hooks/ApiHooks/InstituteAutomate/institute-automate-manager-api";

const SubjectMasterConfig = () => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const params = useParams();

  const [disabled, setDisabled] = useState(false);

  const [fileUpload] = useFileUpload();
  const [automateInstituteStreamSubjectMasterCo] =
    useAutomateInstituteStreamSubjectMasterCo();
  const [automateInstituteStreamSubjectMasterTeachingPlan] =
    useAutomateInstituteStreamSubjectMasterTeachingPlan();

  const onBack = () => {
    navigate(`/q/${params.username}/automate/institute?type=stream-master`, {
      state: {
        ...getQuery.state,
        subject: null,
      },
    });
  };

  //   console.info("getQuery", getQuery.state);

  const onExcelFileTeachingUpload = (e) => {
    if (e.target.files?.length) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey && getQuery.state?.subject?._id) {
            automateInstituteStreamSubjectMasterTeachingPlan({
              asmid: getQuery.state?.subject?._id,
              excel_file: res.data?.imageKey,
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  const onExcelFileCoUpload = (e) => {
    if (e.target.files?.length) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey && getQuery.state?.subject?._id) {
            automateInstituteStreamSubjectMasterCo({
              asmid: getQuery.state?.subject?._id,
              excel_file: res.data?.imageKey,
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div className={style.fm_header_start_container}>
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onBack}
              />
              <h6>{getQuery.state?.subject?.subjectName ?? ""} </h6>
            </div>
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "flex-start",
                gap: "1rem",
              }}
            >
              <UploadExcelButton
                onExcelFileUpload={onExcelFileTeachingUpload}
                dynamicKey="teaching_plan"
                label={t("teaching_plan")}
              />
              <UploadExcelButton
                onExcelFileUpload={onExcelFileCoUpload}
                label={t("co_excel")}
                dynamicKey="co_plan"
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={`/q/${params.username}/automate/institute?type=subject${tab.urlKey}`}
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginBlock: "0",
          }}
        />
        <UserMemberContentWrapper>
          <Suspense fallback={<QvipleLoader />}>
            {searchParams.get("n") === "co" ? (
              <SubjectMasterCo />
            ) : (
              <SubjectMasterTeachingPlan />
            )}
          </Suspense>
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default SubjectMasterConfig;

const tabList = [
  {
    label: "teaching_plan",
    id: "teaching_plan",
    urlKey: "",
    groupIs: ["", null, "tutorial", "practical"],
  },
  {
    label: "co",
    id: "co",
    urlKey: "&n=co",
    groupIs: ["co"],
  },
];
