import { useLocation } from "react-router-dom";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import UploadExcelButton from "../../../../Ui/Button/UploadExcelButton";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import { useFileUpload } from "../../../../Hooks/ApiHooks/Auth/auth-api";
import { useEffect, useState } from "react";
import { useAutomateInstituteStreamPo } from "../../../../Hooks/ApiHooks/InstituteAutomate/institute-automate-manager-api";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { useTranslation } from "react-i18next";
import StreamPoCard from "./StreamPoCard";

const StreamPo = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [clsList, setClsList] = useState([]);
  const [disabled, setDisabled] = useState(false);

  const [fileUpload] = useFileUpload();
  const [automateInstituteStreamPo] = useAutomateInstituteStreamPo();

  useEffect(() => {
    if (getQuery.state?.stream?.po?.length) {
      setClsList(getQuery.state?.stream?.po);
    }
  }, [getQuery.state?.stream?.po]);

  const onExcelFileUpload = (e) => {
    if (e.target.files?.length) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey && getQuery.state?.stream?._id) {
            automateInstituteStreamPo({
              stid: getQuery.state?.stream?._id,
              excel_file: res.data?.imageKey,
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  const onSearchEvent = (e) => {
    if (e.target.value) {
      let dt = getFilterPo(getQuery.state?.stream?.po, e.target.value);
      setClsList(dt);
    } else {
      setClsList(getQuery.state?.stream?.po);
    }
  };
  return (
    <>
      <div className={style.fm_header_container}>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
              />
            </div>
          </section>
          <UploadExcelButton
            onExcelFileUpload={onExcelFileUpload}
            label={t("po_excel")}
            customStyle={{
              width: "7rem",
            }}
          />
        </div>
      </div>
      {clsList?.map((po) => (
        <StreamPoCard po={po} key={po?._id} />
      ))}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default StreamPo;

const getFilterPo = (list, search) => {
  let tl = list?.filter((st) => {
    if (st?.attainment_name?.toLowerCase()?.includes(search?.toLowerCase())) {
      return st;
    } else {
      return null;
    }
  });
  return tl;
};
