import { useLocation } from "react-router-dom";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import { useTranslation } from "react-i18next";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import {
  useAutomateInstituteClassSubjectList,
  useAutomateInstituteStreamSubjectMaster,
} from "../../../../Hooks/ApiHooks/InstituteAutomate/institute-automate-manager-api";
import StreamSubjectMasterCard from "./StreamSubjectMasterCard";
import UploadExcelButton from "../../../../Ui/Button/UploadExcelButton";
import { useFileUpload } from "../../../../Hooks/ApiHooks/Auth/auth-api";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import CreateButton from "../../../../Ui/Button/CreateButton";
import StreamAddSubjectMaster from "./StreamAddSubjectMaster";
const StreamSubjectMaster = () => {
  const getQuery = useLocation();
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [add, setAdd] = useState(false);

  const [disabled, setDisabled] = useState(false);

  const [fileUpload] = useFileUpload();
  const [automateInstituteStreamSubjectMaster] =
    useAutomateInstituteStreamSubjectMaster();

  const {
    automateInstituteClassSubjectList,
    automateInstituteClassSubjectListLoading,
    automateInstituteClassSubjectListRefetch,
  } = useAutomateInstituteClassSubjectList({
    data: {
      cid: getQuery.state?.clsId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.clsId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (automateInstituteClassSubjectList?.list?.length) {
      let obj = nextPage(automateInstituteClassSubjectList?.list?.length);
      setState(() => obj);
    }
  }, [automateInstituteClassSubjectList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );
  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    automateInstituteClassSubjectListRefetch();
  };
  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onExcelFileUpload = (e) => {
    if (e.target.files?.length) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (
            res.data?.imageKey &&
            getQuery.state?.stream?._id &&
            getQuery.state?.clsId
          ) {
            automateInstituteStreamSubjectMaster({
              stid: getQuery.state?.stream?._id,
              excel_file: res.data?.imageKey,
              clsId: getQuery.state?.clsId,
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>
              {t("subject_master")} - ({getQuery.state?.clsName ?? ""})
            </h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                    style={{
                      minWidth: "300px",
                    }}
                  />
                </div>
              </section>
              <UploadExcelButton
                onExcelFileUpload={onExcelFileUpload}
                label={t("subject_excel")}
              />
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {automateInstituteClassSubjectList?.list?.map((subject, index) =>
            automateInstituteClassSubjectList?.list?.length === index + 1 ? (
              <div key={subject?._id} ref={ref}>
                <StreamSubjectMasterCard subject={subject} />
              </div>
            ) : (
              <StreamSubjectMasterCard subject={subject} key={subject?._id} />
            )
          )}
          {automateInstituteClassSubjectListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            automateInstituteClassSubjectList?.list?.length === 0 && (
              <EmptyMessage
                title="No subject masters found related this search."
                customStyleContainer={{
                  marginTop: "10rem",
                }}
              />
            )
          ) : (
            automateInstituteClassSubjectList?.list?.length === 0 && (
              <EmptyMessage
                title="No subject masters."
                customStyleContainer={{
                  marginTop: "10rem",
                }}
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>

      {disabled && <QvipleLoader />}
      {add && (
        <StreamAddSubjectMaster
          onClose={onToggle}
          onRefetch={onRefetch}
          clsId={getQuery.state?.clsId}
          streamId={getQuery.state?.stream?._id}
        />
      )}
    </>
  );
};

export default StreamSubjectMaster;
