import { useCallback, useEffect, useState } from "react";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { useTranslation } from "react-i18next";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import {
  useAutomateInstituteUniversityDepartmentStreamList,
  useAutomateInstituteStreamType,
} from "../../../Hooks/ApiHooks/InstituteAutomate/institute-automate-manager-api";
import UploadExcelButton from "../../../Ui/Button/UploadExcelButton";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import AutomateInstituteStreamCard from "./AutomateInstituteStreamCard";
import { useLocation } from "react-router-dom";
import CreateButton from "../../../Ui/Button/CreateButton";
import AutomateAddInstituteStream from "./AutomateAddInstituteStream";
const AutomateInstituteStream = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [disabled, setDisabled] = useState(false);
  const [add, setAdd] = useState(false);

  const [fileUpload] = useFileUpload();
  const [automateInstituteStreamType] = useAutomateInstituteStreamType();

  const {
    automateInstituteUniversityDepartmentStreamList,
    automateInstituteUniversityDepartmentStreamListLoading,
    automateInstituteUniversityDepartmentStreamListRefetch,
  } = useAutomateInstituteUniversityDepartmentStreamList({
    data: {
      page: page,
      limit: 10,
      search: search,
      did: getQuery.state?.departmentId,
      institute_type_id: getQuery.state?.institute_type_id,
      university_id: getQuery.state?.university_id,
    },
    skip: !getQuery.state?.departmentId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (automateInstituteUniversityDepartmentStreamList?.list?.length) {
      let obj = nextPage(
        automateInstituteUniversityDepartmentStreamList?.list?.length
      );
      setState(() => obj);
    }
  }, [automateInstituteUniversityDepartmentStreamList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    automateInstituteUniversityDepartmentStreamListRefetch();
  };

  const onExcelFileUpload = (e) => {
    if (e.target.files?.length) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (
            res.data?.imageKey &&
            getQuery.state?.university_id &&
            getQuery.state?.institute_type_id &&
            getQuery.state?.departmentId
          ) {
            automateInstituteStreamType({
              excel_file: res.data?.imageKey,
              did: getQuery.state?.departmentId,
              institute_type_id: getQuery.state?.institute_type_id,
              university_id: getQuery.state?.university_id,
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };
  // console.info("stasdas", getQuery.state);
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>
              {t("all_stream")} - ({getQuery.state?.departmentName ?? ""})
            </h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <UploadExcelButton
                onExcelFileUpload={onExcelFileUpload}
                label={t("stream_excel")}
              />
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {automateInstituteUniversityDepartmentStreamList?.list?.map(
            (stream, index) =>
              automateInstituteUniversityDepartmentStreamList?.list?.length ===
              index + 1 ? (
                <div key={stream?._id} ref={ref}>
                  <AutomateInstituteStreamCard stream={stream} />
                </div>
              ) : (
                <AutomateInstituteStreamCard
                  stream={stream}
                  key={stream?._id}
                />
              )
          )}
          {automateInstituteUniversityDepartmentStreamListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            automateInstituteUniversityDepartmentStreamList?.list?.length ===
              0 && <EmptyMessage title="No stream found related this search." />
          ) : (
            automateInstituteUniversityDepartmentStreamList?.list?.length ===
              0 && <EmptyMessage title="No stream." />
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
      {add && (
        <AutomateAddInstituteStream
          onClose={onToggle}
          onRefetch={onRefetch}
          departmentId={getQuery.state?.departmentId}
          instituteTypeId={getQuery.state?.institute_type_id}
          universityId={getQuery.state?.university_id}
        />
      )}
    </>
  );
};

export default AutomateInstituteStream;
