import { useTranslation } from "react-i18next";
import { Link, useLocation, useParams } from "react-router-dom";
import style from "../../../InstituteFeedback/InstituteFeedback.module.css";

const StreamSubjectMasterCard = ({ subject }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  return (
    <Link
      to={`/q/${params.username}/automate/institute?type=subject`}
      state={{
        ...getQuery.state,
        subject: subject,
      }}
    >
      <div className={style.sf_card}>
        <div className={style.sf_card_contatiner}>
          <div className={style.sf_card_contatiner_inner}>
            <h6>
              {t("subject_name")}
              {": "}
              {subject?.subjectName ?? ""}
            </h6>
            <p>
              {t("subject_type")} {": "} {subject?.subjectType ?? 0}
            </p>
            <p>
              {t("theory")} {": "} {subject?.is_theory ? "Yes" : "No"}
            </p>
            <p>
              {t("tutorial")} {": "} {subject?.is_tutorial ? "Yes" : "No"}
            </p>
            <p>
              {t("practical")} {": "} {subject?.is_practical ? "Yes" : "No"}
            </p>
          </div>
        </div>
      </div>
    </Link>
  );
};
export default StreamSubjectMasterCard;
