import { useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../../Utils/Functions/nextPage";
import { useTranslation } from "react-i18next";
import { useAutomateInstituteSubjectChapterTopicList } from "../../../../../Hooks/ApiHooks/InstituteAutomate/institute-automate-manager-api";
import style from "../../../AutomateInstitute.module.css";
import QvipleLoader from "../../../../../Loader/QvipleLoader";
import Loading from "../../../../../Loader/Loading";
import EmptyMessage from "../../../../../Utils/EmptyMessage/EmptyMessage";
const ChapterTopicList = ({ cid }) => {
  const { t } = useTranslation();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView();

  const {
    automateInstituteSubjectChapterTopicList,
    automateInstituteSubjectChapterTopicListLoading,
  } = useAutomateInstituteSubjectChapterTopicList({
    data: {
      cid: cid,
      page: page,
      limit: 10,
      search: "",
    },
    skip: !cid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (automateInstituteSubjectChapterTopicList?.list?.length) {
      let obj = nextPage(
        automateInstituteSubjectChapterTopicList?.list?.length
      );
      setState(() => obj);
    }
  }, [automateInstituteSubjectChapterTopicList?.list]);

  return (
    <>
      {automateInstituteSubjectChapterTopicList?.list?.map((topic, index) =>
        automateInstituteSubjectChapterTopicList?.list?.length === index + 1 ? (
          <div className={style.ctlc_card} key={topic?._id} ref={ref}>
            <div className={style.ctlcc_container}>
              <h6>{topic?.topic_name ?? ""}</h6>
              <p>
                {t("scheduled_on")} {topic?.topic_last_date}
              </p>
              {topic?.topic_link ? (
                <p>
                  {t("link")} {topic?.topic_link}
                </p>
              ) : null}
            </div>
          </div>
        ) : (
          <div className={style.ctlc_card} key={topic?._id}>
            <div className={style.ctlcc_container}>
              <h6>{topic?.topic_name ?? ""}</h6>
              <p>
                {t("scheduled_on")} {topic?.topic_last_date}
              </p>
              {topic?.topic_link ? (
                <p>
                  {t("link")} {topic?.topic_link}
                </p>
              ) : null}
            </div>
          </div>
        )
      )}

      {automateInstituteSubjectChapterTopicListLoading ? (
        <Loading />
      ) : (
        automateInstituteSubjectChapterTopicList?.list?.length === 0 && (
          <EmptyMessage title="No topics." />
        )
      )}
    </>
  );
};

export default ChapterTopicList;
