import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import ChpaterTeachingPlanCard from "./ChapterTeachingPlanCard";
import QvipleLoader from "../../../../../Loader/QvipleLoader";
import Loading from "../../../../../Loader/Loading";
import EmptyMessage from "../../../../../Utils/EmptyMessage/EmptyMessage";
import { useAutomateInstituteSubjectChapterList } from "../../../../../Hooks/ApiHooks/InstituteAutomate/institute-automate-manager-api";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../../Utils/Functions/debounce";
const SubjectPracticalTeachingPlan = ({ smid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    automateInstituteSubjectChapterList,
    automateInstituteSubjectChapterListLoading,
  } = useAutomateInstituteSubjectChapterList({
    data: {
      smid: smid,
      page: page,
      limit: 10,
      search: search,
      flow: "Practical",
    },
    skip: !smid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (automateInstituteSubjectChapterList?.list?.length) {
      let obj = nextPage(automateInstituteSubjectChapterList?.list?.length);
      setState(() => obj);
    }
  }, [automateInstituteSubjectChapterList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  return (
    <>
      <div
        className={style.fm_search_full}
        style={{
          marginBottom: "0.5rem",
        }}
      >
        <section
          className={style.fm_search_full_container}
          style={{
            gap: "1rem",
          }}
        >
          <div
            className={style.fm_search_full_container_inner}
            style={{
              flex: "1.5 1.5",
            }}
          >
            <img
              className={style.fm_search_full_icon}
              alt="search icon"
              src={`${assestsNavbarUrl}/navbar-search.svg`}
            />
            <input
              type="text"
              placeholder={t("search")}
              onChange={onSearchEvent}
            />
          </div>
        </section>
      </div>

      {automateInstituteSubjectChapterList?.list?.map((chapter, index) =>
        automateInstituteSubjectChapterList?.list?.length === index + 1 ? (
          <div key={chapter?._id} ref={ref}>
            <ChpaterTeachingPlanCard chapter={chapter} />
          </div>
        ) : (
          <ChpaterTeachingPlanCard chapter={chapter} key={chapter?._id} />
        )
      )}

      {automateInstituteSubjectChapterListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        automateInstituteSubjectChapterList?.list?.length === 0 && (
          <EmptyMessage title="No chpaters found related this search." />
        )
      ) : (
        automateInstituteSubjectChapterList?.list?.length === 0 && (
          <EmptyMessage title="No chpaters." />
        )
      )}
    </>
  );
};

export default SubjectPracticalTeachingPlan;
