import { useState } from "react";
import { useTranslation } from "react-i18next";
import style from "../../../AutomateInstitute.module.css";
import { assestsAutomateUrl } from "../../../../../Services/UrlConfig/AssestsBaseUrl";
import ChapterTopicList from "./ChapterTopicList";
import BorderBottom from "../../../../../Ui/Border/BorderBottom";
const ChapterTeachingPlanCard = ({ chapter }) => {
  const { t } = useTranslation();
  const [isOpen, setIsOpen] = useState(false);
  const onToggle = () => {
    setIsOpen((pre) => !pre);
  };
  return (
    <>
      <section className={style.ctpc_card}>
        <div className={style.ctpcc_conatiner} onClick={onToggle}>
          <img src={`${assestsAutomateUrl}/chapter.svg`} alt="topic icon" />
          <h6>{chapter?.chapter_name ?? ""}</h6>
          <img
            src={
              isOpen
                ? `${assestsAutomateUrl}/arrow-fill-down.svg`
                : `${assestsAutomateUrl}/arrow-fill.svg`
            }
            alt="down icon"
          />
        </div>
        <p
          style={{
            marginLeft: "1rem",
            marginTop: "0.5rem",
          }}
        >
          {t("total_topic")} {": "} {chapter?.topic_count ?? 0}
        </p>

        {chapter?.chapter_link ? (
          <p
            style={{
              marginLeft: "1rem",
            }}
          >
            {t("link")} {chapter?.chapter_link ?? ""}
          </p>
        ) : null}
        <BorderBottom />
        {isOpen && <ChapterTopicList cid={chapter?._id} />}
      </section>
    </>
  );
};

export default ChapterTeachingPlanCard;
