import { Suspense, lazy } from "react";
import { useSearchParams } from "react-router-dom";
import InstituteNavbarConfig from "../Config/InstituteNavbarConfig";
import InstituteMemberConfig from "../Config/InstituteMemberConfig";
import QvipleLoader from "../../Loader/QvipleLoader";
import GoBack from "../../Shared/StaffStudentProfile/GoBack";
import AutomateInstituteStream from "./AutomateInstituteStream/AutomateInstituteStream";
import StreamMasterConfig from "./AutomateInstituteStream/StreamMaster/StreamMasterConfig";
import SubjectMasterConfig from "./AutomateInstituteStream/StreamMaster/SubjectMaster/SubjectMasterConfig";
// import AutomateInstituteProfile from "./AutomateInstituteProfile";
import AutomateInstituteType from "./AutomateInstituteType/AutomateInstituteType";
import AutomateInstituteUniversity from "./AutomateInstituteUniversity/AutomateInstituteUniversity";
import AutomateInstituteDepartmentType from "./AutomateInstituteDepartmentType/AutomateInstituteDepartmentType";
import { useSelector } from "react-redux";
import StreamSubjectMaster from "./AutomateInstituteStream/StreamMaster/StreamSubjectMaster";
const AutomateInstitute = () => {
  const [searchParams] = useSearchParams();
  const instituteId = useSelector((state) => state.idChange.id);

  return (
    <InstituteNavbarConfig>
      <InstituteMemberConfig>
        <section
          style={{
            width: "100%",
          }}
        >
          <GoBack
            customStyle={{
              width: "100%",
              marginTop: "0",
              marginBottom: "1rem",
            }}
          />
          <Suspense fallback={<QvipleLoader />}>
            {searchParams.get("type") === "stream-master" ? (
              <StreamMasterConfig />
            ) : searchParams.get("type") === "standard-subject" ? (
              <StreamSubjectMaster />
            ) : searchParams.get("type") === "subject" ? (
              <SubjectMasterConfig />
            ) : searchParams.get("type") === "stream" ? (
              <AutomateInstituteStream />
            ) : searchParams.get("type") === "university" ? (
              <AutomateInstituteUniversity instituteId={instituteId} />
            ) : searchParams.get("type") === "department" ? (
              <AutomateInstituteDepartmentType instituteId={instituteId} />
            ) : (
              <AutomateInstituteType instituteId={instituteId} />
            )}

            {/* {searchParams.get("type") === "stream-master" ? (
              <StreamMasterConfig />
            ) : searchParams.get("type") === "subject" ? (
              <SubjectMasterConfig />
            ) : searchParams.get("type") === "stream" ? (
              <AutomateInstituteStream />
            ) : searchParams.get("type") === "institute" ? (
              <AutomateInstituteType instituteId={instituteId} />
            ) : searchParams.get("type") === "university" ? (
              <AutomateInstituteUniversity instituteId={instituteId} />
            ) : searchParams.get("type") === "department" ? (
              <AutomateInstituteDepartmentType instituteId={instituteId} />
            ) : (
              <AutomateInstituteProfile />
            )} */}
          </Suspense>
        </section>
      </InstituteMemberConfig>
    </InstituteNavbarConfig>
  );
};

export default AutomateInstitute;
