import { useState } from "react";
import { useTranslation } from "react-i18next";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import FormInput from "../../../../Ui/Input/FormInput";
import style from "../../../../UserStaffMember/FinanceManager/Income/Income.module.css";
import { useAutomateInstituteStreamManuallySubjectMaster } from "../../../../Hooks/ApiHooks/InstituteAutomate/institute-automate-manager-api";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import TileCard from "../../../../Ui/Tab/Tile/TileCard";
import TileWrapper from "../../../../Ui/Tab/Tile/TileWrapper";
const StreamAddSubjectMaster = ({ onClose, onRefetch, streamId, clsId }) => {
  const { t } = useTranslation();
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    subjectName: "",
    subjectType: "",
    is_practical: "",
    is_tutorial: "",
    is_theory: "",
  });

  const [automateInstituteStreamManuallySubjectMaster] =
    useAutomateInstituteStreamManuallySubjectMaster();

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTile = (value) => {
    setState((prev) => ({
      ...prev,
      subjectType: value,
    }));
  };

  const onTheoryTile = (value) => {
    setState((prev) => ({
      ...prev,
      is_theory: value,
    }));
  };

  const onTutorialTile = (value) => {
    setState((prev) => ({
      ...prev,
      is_tutorial: value,
    }));
  };

  const onPracticalTile = (value) => {
    setState((prev) => ({
      ...prev,
      is_practical: value,
    }));
  };

  const onAdd = () => {
    let validation = requiredField(state, []);
    if (existingRequiredField(validation) && streamId && clsId) {
      setDisabled((pre) => !pre);
      automateInstituteStreamManuallySubjectMaster({
        stid: streamId,
        bodyParameter: {
          ...state,
          clsId: clsId,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    } else {
      setErrorField(validation);
    }
  };

  // console.info("stid", errorField, streamId, clsId);
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("add_new_subject_master")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <FormInput
              label={t("name")}
              placeholder={t("stubject_placeholder")}
              name={"subjectName"}
              value={state.subjectName}
              type={"text"}
              onChange={onChange}
              errorShow={errorField["subjectName"]}
            />
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "space-between",
                gap: "1rem",
              }}
            >
              <TileWrapper label="subject_type">
                {subject_type?.map((tile) => (
                  <TileCard
                    key={tile?.key}
                    tile={tile}
                    onTile={onTile}
                    activeTile={state?.subjectType}
                  />
                ))}
              </TileWrapper>
              <TileWrapper label="theory">
                {other_type?.map((tile) => (
                  <TileCard
                    key={tile?.key}
                    tile={tile}
                    onTile={onTheoryTile}
                    activeTile={state?.is_theory}
                  />
                ))}
              </TileWrapper>
            </div>
            <div
              style={{
                display: "flex",
                alignItems: "center",
                justifyContent: "space-between",
                gap: "1rem",
              }}
            >
              <TileWrapper label="tutorial">
                {other_type?.map((tile) => (
                  <TileCard
                    key={tile?.key}
                    tile={tile}
                    onTile={onTutorialTile}
                    activeTile={state?.is_tutorial}
                  />
                ))}
              </TileWrapper>
              <TileWrapper label="practical">
                {other_type?.map((tile) => (
                  <TileCard
                    key={tile?.key}
                    tile={tile}
                    onTile={onPracticalTile}
                    activeTile={state?.is_practical}
                  />
                ))}
              </TileWrapper>
            </div>

            <CreateButton label="add" onAction={onAdd} />
          </UserMemberContentWrapper>
          {disabled && <QvipleLoader />}
        </div>
      </Modal>
    </>
  );
};

export default StreamAddSubjectMaster;

export const subject_type = [
  {
    key: "Mandatory",
    name: "mandatory",
  },
  {
    key: "Optional",
    name: "optional",
  },
];

export const other_type = [
  {
    key: "Yes",
    name: "yes",
  },
  {
    key: "No",
    name: "no",
  },
];
