import { useTranslation } from "react-i18next";
import style from "../../InstituteFeedback/InstituteFeedback.module.css";
import { Link, useLocation, useParams } from "react-router-dom";

const AutomateInstituteUniversityCard = ({ university }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  return (
    <Link
      to={`/q/${params.username}/automate/institute?type=department`}
      state={{
        ...getQuery.state,
        university_id: university?._id,
        universityName: university?.name,
      }}
    >
      <div className={style.sf_card}>
        <div className={style.sf_card_contatiner}>
          <div className={style.sf_card_contatiner_inner}>
            <h6
              style={{
                marginBottom: "0",
              }}
            >
              {t("uinversity_name")}
              {": "}
              {university?.name ?? ""}
            </h6>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default AutomateInstituteUniversityCard;
