import { useTranslation } from "react-i18next";
import {
  useLocation,
  useNavigate,
  useParams,
  useSearchParams,
} from "react-router-dom";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUtilityUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import HorizontalTabWrapper from "../../../../Ui/Tab/Horizontal/HorizontalTabWrapper";
import HorizontalTabCard from "../../../../Ui/Tab/Horizontal/HorizontalTabCard";
import { Suspense } from "react";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import StreamClassMaster from "./StreamClassMaster";
// import StreamSubjectMaster from "./StreamSubjectMaster";
import StreamPo from "./StreamPo";

const StreamMasterConfig = () => {
  const { t } = useTranslation();
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const params = useParams();

  const onBack = () => {
    navigate(`/q/${params.username}/automate/institute`);
  };

  // console.info("getQuery", getQuery.state);

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <div className={style.fm_header_start_container}>
              <img
                alt="search icon"
                src={`${assestsUtilityUrl}/back.svg`}
                style={{
                  height: "2rem",
                  width: "2rem",
                }}
                onClick={onBack}
              />
              {getQuery.state?.stream?.isFirstYear ? (
                <h6>
                  {getQuery.state?.departmentName ?? ""} {" - "}(
                  {getQuery.state?.universityName ?? ""}) {" - "}(
                  {getQuery.state?.typeName ?? ""})
                </h6>
              ) : (
                <h6>
                  {getQuery.state?.stream?.name ?? ""}
                  {" - "}({getQuery.state?.stream?.department_type?.name ?? ""}){" "}
                  {" - "}({getQuery.state?.stream?.affiliated_with?.name ?? ""}){" "}
                  {" - "}({getQuery.state?.stream?.institute_type?.name ?? ""})
                </h6>
              )}
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />

        <HorizontalTabWrapper>
          {tabList.map((tab) => (
            <HorizontalTabCard
              key={tab?.id}
              tab={tab}
              isLink
              isActive={tab.groupIs?.includes(searchParams.get("n"))}
              parentState={getQuery.state}
              url={`/q/${params.username}/automate/institute?type=stream-master${tab.urlKey}`}
            />
          ))}
        </HorizontalTabWrapper>
        <BorderBottom
          customStyleBorder={{
            marginBlock: "0",
          }}
        />
        <UserMemberContentWrapper>
          <Suspense fallback={<QvipleLoader />}>
            {!searchParams.get("n") ? (
              <StreamClassMaster />
            ) : // searchParams.get("n") === "subject" ? (
            //   <StreamSubjectMaster />
            // ) :
            searchParams.get("n") === "po" ? (
              <StreamPo />
            ) : null}
          </Suspense>
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default StreamMasterConfig;

const tabList = [
  {
    label: "standard",
    id: "standard",
    urlKey: "",
    groupIs: ["", null],
  },
  // {
  //   label: "automate_subject",
  //   id: "subject",
  //   urlKey: "&n=subject",
  //   groupIs: ["subject"],
  // },
  {
    label: "po",
    id: "po",
    urlKey: "&n=po",
    groupIs: ["po"],
  },
];
