import { useCallback, useEffect, useState } from "react";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import { useTranslation } from "react-i18next";
import style from "../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import {
  useAutomateInstituteType,
  useAutomateInstituteTypeList,
} from "../../../Hooks/ApiHooks/InstituteAutomate/institute-automate-manager-api";
import UploadExcelButton from "../../../Ui/Button/UploadExcelButton";
import { useFileUpload } from "../../../Hooks/ApiHooks/Auth/auth-api";
import AutomateInstituteTypeCard from "./AutomateInstituteTypeCard";
import CreateButton from "../../../Ui/Button/CreateButton";
import AutomateAddInstituteType from "./AutomateAddInstituteType";
const AutomateInstituteType = ({ instituteId }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [disabled, setDisabled] = useState(false);
  const [add, setAdd] = useState(false);

  const [fileUpload] = useFileUpload();
  const [automateInstituteType] = useAutomateInstituteType();

  const {
    automateInstituteTypeList,
    automateInstituteTypeListLoading,
    automateInstituteTypeListRefetch,
  } = useAutomateInstituteTypeList({
    data: {
      id: instituteId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !instituteId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (automateInstituteTypeList?.list?.length) {
      let obj = nextPage(automateInstituteTypeList?.list?.length);
      setState(() => obj);
    }
  }, [automateInstituteTypeList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onToggle = () => {
    setAdd((pre) => !pre);
  };

  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    automateInstituteTypeListRefetch();
  };
  const onExcelFileUpload = (e) => {
    if (e.target.files?.length) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey && instituteId) {
            automateInstituteType({
              id: instituteId,
              excel_file: res.data?.imageKey,
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("all_institute_type")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                    style={{
                      minWidth: "300px",
                    }}
                  />
                </div>
              </section>
              <UploadExcelButton
                onExcelFileUpload={onExcelFileUpload}
                label={t("institute_type_excel")}
              />
              <CreateButton
                label="add"
                customStyle={{
                  paddingBlock: "0.4rem",
                  margin: "0",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {automateInstituteTypeList?.list?.map((instituteType, index) =>
            automateInstituteTypeList?.list?.length === index + 1 ? (
              <div key={instituteType?._id} ref={ref}>
                <AutomateInstituteTypeCard instituteType={instituteType} />
              </div>
            ) : (
              <AutomateInstituteTypeCard
                instituteType={instituteType}
                key={instituteType?._id}
              />
            )
          )}
          {automateInstituteTypeListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            automateInstituteTypeList?.list?.length === 0 && (
              <EmptyMessage title="No institute type found related this search." />
            )
          ) : (
            automateInstituteTypeList?.list?.length === 0 && (
              <EmptyMessage title="No institute type." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
      {add && (
        <AutomateAddInstituteType
          onClose={onToggle}
          onRefetch={onRefetch}
          instituteId={instituteId}
        />
      )}
    </>
  );
};

export default AutomateInstituteType;
