import { useLocation } from "react-router-dom";
import StreamClassMasterCard from "./StreamClassMasterCard";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import UploadExcelButton from "../../../../Ui/Button/UploadExcelButton";
import style from "../../../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import { useFileUpload } from "../../../../Hooks/ApiHooks/Auth/auth-api";
import { useCallback, useEffect, useState } from "react";
import {
  useAutomateInstituteStreamClassList,
  useAutomateInstituteStreamClassMaster,
} from "../../../../Hooks/ApiHooks/InstituteAutomate/institute-automate-manager-api";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { useTranslation } from "react-i18next";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import StreamAddClassMaster from "./StreamAddClassMaster";

const StreamClassMaster = () => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [disabled, setDisabled] = useState(false);
  const [add, setAdd] = useState(false);

  const [fileUpload] = useFileUpload();
  const [automateInstituteStreamClassMaster] =
    useAutomateInstituteStreamClassMaster();

  const {
    automateInstituteStreamClassList,
    automateInstituteStreamClassListLoading,
    automateInstituteStreamClassListRefetch,
  } = useAutomateInstituteStreamClassList({
    data: {
      page: page,
      limit: 10,
      search: search,
      stid: getQuery.state?.stream?._id,
    },
    skip: !getQuery.state?.stream?._id,
  });

  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (automateInstituteStreamClassList?.list?.length) {
      let obj = nextPage(automateInstituteStreamClassList?.list?.length);
      setState(() => obj);
    }
  }, [automateInstituteStreamClassList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  const onRefetch = () => {
    setPage(() => 1);
    setSearch("");
    automateInstituteStreamClassListRefetch();
  };

  const onExcelFileUpload = (e) => {
    if (e.target.files?.length) {
      setDisabled((pre) => !pre);
      const formData = new FormData();
      formData.append("file", e.target.files[0]);
      fileUpload({
        fileUpload: formData,
      })
        .then((res) => {
          if (res.data?.imageKey && getQuery.state?.stream?._id) {
            automateInstituteStreamClassMaster({
              stid: getQuery.state?.stream?._id,
              excel_file: res.data?.imageKey,
            })
              .then(() => {
                setDisabled((pre) => !pre);
              })
              .catch({});
          } else {
            setDisabled((pre) => !pre);
          }
        })
        .catch({});
    }
  };

  return (
    <>
      <div className={style.fm_header_container}>
        <div className={style.fm_search_full}>
          <section className={style.fm_search_full_container}>
            <div className={style.fm_search_full_container_inner}>
              <img
                className={style.fm_search_full_icon}
                alt="search icon"
                src={`${assestsNavbarUrl}/navbar-search.svg`}
              />
              <input
                type="text"
                placeholder={t("search")}
                onChange={onSearchEvent}
              />
            </div>
          </section>
          <UploadExcelButton
            onExcelFileUpload={onExcelFileUpload}
            label={t("standard_excel")}
            customStyle={{
              width: "11rem",
            }}
          />
          <CreateButton
            label="add"
            customStyle={{
              paddingBlock: "0.4rem",
              margin: "0",
            }}
            onAction={onToggle}
          />
        </div>
      </div>
      {automateInstituteStreamClassList?.list?.map((cls, index) =>
        automateInstituteStreamClassList?.list?.length === index + 1 ? (
          <div key={cls?._id} ref={ref}>
            <StreamClassMasterCard cls={cls} key={cls?._id} />
          </div>
        ) : (
          <StreamClassMasterCard cls={cls} key={cls?._id} />
        )
      )}
      {automateInstituteStreamClassListLoading ? (
        search ? (
          <QvipleLoader />
        ) : (
          <Loading />
        )
      ) : search ? (
        automateInstituteStreamClassList?.list?.length === 0 && (
          <EmptyMessage title="No standard found related this search." />
        )
      ) : (
        automateInstituteStreamClassList?.list?.length === 0 && (
          <EmptyMessage title="No standard." />
        )
      )}
      {disabled && <QvipleLoader />}
      {add && (
        <StreamAddClassMaster
          onClose={onToggle}
          onRefetch={onRefetch}
          streamId={getQuery.state?.stream?._id}
        />
      )}
    </>
  );
};

export default StreamClassMaster;
