import { useLocation } from "react-router-dom";
import SubjectMasterCoCard from "./SubjectMasterCoCard";

const SubjectMasterCo = () => {
  const getQuery = useLocation();
  return (
    <>
      {getQuery.state?.subject?.co?.map((co_attainment) => (
        <SubjectMasterCoCard
          co_attainment={co_attainment}
          key={co_attainment?._id}
        />
      ))}
    </>
  );
};

export default SubjectMasterCo;
