import { useTranslation } from "react-i18next";
import style from "../../InstituteFeedback/InstituteFeedback.module.css";
import { Link, useLocation, useParams } from "react-router-dom";

const AutomateInstituteTypeCard = ({ instituteType }) => {
  const { t } = useTranslation();
  const params = useParams();
  const getQuery = useLocation();
  return (
    <Link
      to={`/q/${params.username}/automate/institute?type=university`}
      state={{
        ...getQuery.state,
        institute_type_id: instituteType?._id,
        typeName: instituteType?.name,
      }}
    >
      <div className={style.sf_card}>
        <div className={style.sf_card_contatiner}>
          <div className={style.sf_card_contatiner_inner}>
            <h6
              style={{
                marginBottom: "0",
              }}
            >
              {t("institute_type")}
              {": "}
              {instituteType?.name ?? ""}
            </h6>
          </div>
        </div>
      </div>
    </Link>
  );
};

export default AutomateInstituteTypeCard;
